body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body{
  height: 100%;
}
main{
  display: flex !important;
  flex-direction: column;
}
.section{
  flex: 1;
}
#root{
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
#root > .container, main {
  flex: 1;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

.notFound{
  max-width: 600px;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
  text-align: center;
}
.notFound img{
  width: 100%;
}

.section-profile-cover {
  z-index: -1;
}

.mt--400{
  margin-top: -28rem;
}

.message{
  position: fixed;
  top: 40px;
  width: 100%;
  z-index: 9999;
}

.form-group .invalid-feedback {
  display: block;
}

/* .fadeOut2 {
  transition: transform .4s ease-out 1s;
  opacity: 0;
} */
.alertClose {
  animation: close 2.1s;
}

@keyframes close {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.projects{
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
}

.projeclist{
  width: 23%;
  margin: 0 1%;
  height: 127px;
}

.project-title{
  display: flex;
  align-items: center;
}

.project-title .badge{
  padding: 3px 10px;
}

.projeclist p {
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}
.cursor{
  cursor: pointer;
}

.editBtn{
  position: absolute;
  right: 15px;
  bottom: 10px;
  cursor: pointer;
}

.table th, .table td{
  max-width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-gray700{
  color: #525f7f;
}

.text-gray700:hover{
  color: #5e72e4;
}

.badge {
  text-transform: none !important;
  font-size: 14px !important;
}
.badge.user_tate {
  display: flex;
  line-height: 24px;
  padding: 0;
}
.user_tate .custom-toggle{
  margin: 0 5px;
}

.cardSelect{
  height: 46px;
  display: flex;
}
.cardSelect>div{
  flex: 1;
}

.snack{
  border-left: 2px solid #adb5bd;
  border-top: 2px solid #adb5bd;
  border-right: 2px solid #fff;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  display: block;
  position: absolute;
  top: 9px;
  left: 17px;
  animation: random 1.2s linear infinite;
}

.btn:disabled{
  box-shadow: 0 4px 6px rgba(50, 50, 93, .11),
  0 1px 3px rgba(0, 0, 0, .08) !important;
}

@keyframes random {
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform:rotate(360deg);
  }
}


@media screen and (max-width: 768px) {
  .projeclist {
    width: 48%;
  }
}

@media screen and (max-width: 420px) {
  .projeclist {
    width: 100%;
  }
}